/**
 * 图片懒加载子组件
 */

import React, { Component } from 'react'

import styles from './LazyImg.scss';

import errorImg from './error.png';
import defaultImg from './default.png';
 
class LazyImg extends Component {
    static defaultProps = {
        _fwb: 50, //距离底部多少距离开始加载图片
        loader: defaultImg, //未加载图片时显示的默认图片
        errorImg: errorImg,
        loaded: false,
        show: false,
    }
    constructor(props) {
        super(props)
        this.state = {
            loader: props.loader,
            loaded: props.loaded,
        }
        this.showImage = false
    }
    componentDidMount() {
        if (!this.showImage && this.props._fwb) {
            this.handleUpdate();
        }
        if (!this.showImage && this.props.show) {
            this.handleLoadImg()
        }
    }
    componentWillReceiveProps(nextProps) {
        if (!this.showImage && nextProps.show) {
            this.handleLoadImg()
        }
    }
    shouldComponentUpdate(nextProps, nextState) {
        return !this.state.loaded
    }
    componentDidUpdate(prevProps) {
        if (!this.showImage && prevProps._fwb) {
            this.handleUpdate();
        }
    }
    handleUpdate = () => {
        let { top, height } = this.ele.getBoundingClientRect();
        let { _vpt, _vph, _fwb } = this.props
        if (_vpt <= (top + height) && top <= (_vpt + _vph + _fwb)) {
            this.handleLoadImg()
        }
    }
    handleLoadImg = () => {
        let { loaded } = this.state
        if (loaded) {
            return
        }
        this.showImage = true
        let { src, errorImg } = this.props
        let img = new Image();
        img.src = src;
        img.onload = () => {
            this.setState({
                loader: src,
                loaded: true,
            })
        };
        img.onerror = () => {
            this.setState({
                loader: errorImg,
            })
        };
    }
    render() {
        let { className, style, alt } = this.props
        let { loader } = this.state
        return (
            <div ref={ele => this.ele = ele} onClick={this.handleLoadImg} className={styles.LazyImg + ' ' + className} style={{ ...style, backgroundImage: 'url(' + loader + ')' }}></div>
        )
    }
}

export default LazyImg


